:root {
  --background-color: #333;
  --border-color: #555;
  --button-bg-color: #555;
  --button-hover-bg-color: #666;
  --calendar-bg-color: #444;
  --grayed-out-color: #888;
  --text-color: white;
  --dark-yellow: #d1a800;
}

* {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
}

header {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

#monthName {
  margin-left: 10px;
}

#navButtons {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

button {
  background-color: var(--button-bg-color);
  color: var(--text-color);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: var(--button-hover-bg-color);
}

#calendar-container {
  background-color: var(--calendar-bg-color);
  border-radius: 10px;
  min-width: 530px;
  max-width: 800px;
  margin: auto;
  padding: 20px;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  border: 1px solid var(--border-color);
  text-align: center;
  padding: 10px;
}

td {
  cursor: pointer;
  min-width: 53px;
}

.today {
  border: 3px solid var(--background-color);
}

.grayed-out {
  color: var(--grayed-out-color);
  opacity: .5;
}

#calendar tbody td {
  vertical-align: top;
  width: 100px;
  height: 80px;
  font-size: 16px;
  position: relative;
}

#calendar tbody td strong {
  margin-bottom: 5px;
  display: block;
}

#calendar tbody td div {
  color: var(--text-color);
  word-break: break-word;
  max-width: 90px;
  margin: auto;
  padding: 5px;
  font-size: 13px;
  line-height: 1.2;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

footer {
  text-align: center;
  background-color: var(--background-color);
  color: var(--text-color);
  border-top: 1px solid var(--border-color);
  margin-top: 20px;
  padding: 10px;
}

footer a {
  color: var(--dark-yellow);
}

footer a:hover {
  color: #fff;
}

.loader {
  z-index: 1000;
  border: 8px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

